<template>
  <div class="journal-entry-container">
    <div class="header">
      <h2>记账凭证</h2>
      <el-button type="primary" @click="showAddDialog">新增凭证</el-button>
    </div>

    <div class="content">
      <el-card>
        <el-form :inline="true" :model="searchForm" class="search-form">
          <el-form-item label="凭证编号">
            <el-input v-model="searchForm.voucherNo" placeholder="请输入凭证编号" />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="search">查询</el-button>
            <el-button @click="resetSearch">重置</el-button>
          </el-form-item>
        </el-form>

        <el-table :data="entryList" style="width: 100%">
          <el-table-column prop="voucherNo" label="凭证编号" />
          <el-table-column prop="voucherDate" label="凭证日期" />
          <el-table-column prop="summary" label="摘要" />
          <el-table-column prop="totalDebit" label="借方总额" />
          <el-table-column prop="totalCredit" label="贷方总额" />
          <el-table-column prop="status" label="状态" :formatter="formatStatus" />
          <el-table-column label="操作" width="220">
            <template slot-scope="scope">
              <el-button size="small" type="primary" @click="editEntry(scope.row)" v-if="scope.row.status === 0">编辑</el-button>
              <el-button size="small" type="danger" @click="cancelEntry(scope.row)" v-if="scope.row.status === 0">取消</el-button>
            </template>
          </el-table-column>
        </el-table>

        <div class="pagination">
          <el-pagination
            :current-page="currentPage"
            :page-size="pageSize"
            :total="total"
            layout="total, prev, pager, next, jumper"
            @current-change="handleCurrentChange"
          />
        </div>
      </el-card>
    </div>

    <!-- 新增/编辑对话框 -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="80%">
      <el-form ref="entryFormRef" :model="entryForm" label-width="120px">
        <el-form-item label="凭证日期">
          <el-date-picker v-model="entryForm.voucherDate" type="date" style="width: 100%" />
        </el-form-item>
        <el-form-item label="摘要">
          <el-input v-model="entryForm.summary" placeholder="请输入摘要" />
        </el-form-item>

        <!-- 凭证明细表 -->
        <el-table :data="entryDetails" style="width: 100%" border>
          <el-table-column prop="subjectName" label="会计科目">
            <template slot-scope="scope">
              <el-select v-model="scope.row.subjectId" placeholder="选择科目" style="width: 100%">
                <el-option v-for="subject in subjectList" :key="subject.id" :label="`${subject.code} - ${subject.name}`" :value="subject.id" />
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="备注">
            <template slot-scope="scope">
              <el-input v-model="scope.row.remark" placeholder="备注" />
            </template>
          </el-table-column>
          <el-table-column label="借方金额">
            <template slot-scope="scope">
              <el-input-number v-model="scope.row.debitAmount" :min="0" @change="updateTotal" />
            </template>
          </el-table-column>
          <el-table-column label="贷方金额">
            <template slot-scope="scope">
              <el-input-number v-model="scope.row.creditAmount" :min="0" @change="updateTotal" />
            </template>
          </el-table-column>
          <el-table-column label="操作" width="80">
            <template slot-scope="scope">
              <el-button type="danger" size="mini" @click="removeDetail(scope.$index)" v-if="entryDetails.length > 1">删除</el-button>
            </template>
          </el-table-column>
        </el-table>

        <div class="add-detail-btn">
          <el-button type="primary" @click="addDetail">新增分录</el-button>
        </div>

        <div class="total-section">
          <div class="total-item">
            <span>合计：</span>
            <span>借方金额：{{ entryForm.totalDebit }}</span>
            <span>贷方金额：{{ entryForm.totalCredit }}</span>
            <span v-if="entryForm.totalDebit !== entryForm.totalCredit" class="error-text">借贷不平</span>
          </div>
        </div>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="submitForm" :disabled="entryForm.totalDebit !== entryForm.totalCredit">保存</el-button>
      </div>
    </el-dialog>

    <!-- 查看详情对话框 -->
    <el-dialog title="凭证详情" :visible.sync="detailVisible" width="80%">
      <el-descriptions :column="1" border>
        <el-descriptions-item label="凭证编号">{{ selectedEntry.voucherNo }}</el-descriptions-item>
        <el-descriptions-item label="凭证日期">{{ selectedEntry.voucherDate }}</el-descriptions-item>
        <el-descriptions-item label="摘要">{{ selectedEntry.summary }}</el-descriptions-item>
        <el-descriptions-item label="状态">{{ formatStatusText(selectedEntry.status) }}</el-descriptions-item>
      </el-descriptions>

      <el-table :data="selectedEntryDetails" style="width: 100%; margin-top: 20px" border>
        <el-table-column prop="subjectCode" label="科目编码" />
        <el-table-column prop="subjectName" label="科目名称" />
        <el-table-column prop="remark" label="备注" />
        <el-table-column prop="debitAmount" label="借方金额" />
        <el-table-column prop="creditAmount" label="贷方金额" />
      </el-table>

      <div class="total-section" style="margin-top: 10px">
        <div class="total-item">
          <span>合计：</span>
          <span>借方金额：{{ selectedEntry.totalDebit }}</span>
          <span>贷方金额：{{ selectedEntry.totalCredit }}</span>
        </div>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="detailVisible = false">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import request from "@/api/request";

export default {
  name: 'JournalEntry',
  data() {
    return {
      entryList: [],
      searchForm: {
        voucherNo: '',
        dateRange: []
      },
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dialogVisible: false,
      detailVisible: false,
      dialogTitle: '新增凭证',
      entryForm: {
        id: '',
        voucherNo: '',
        voucherDate: '',
        summary: '',
        totalDebit: 0,
        totalCredit: 0,
        status: 0
      },
      entryDetails: [],
      subjectList: [],
      selectedEntry: {},
      selectedEntryDetails: []
    }
  },
  created() {
    this.loadSubjectList();
    this.loadData();
  },
  methods: {
    async loadData() {
      try {
        const res = await request.get('/journal-entry/page', {
          params: {
            page: this.currentPage,
            pageSize: this.pageSize,
            voucherNo: this.searchForm.voucherNo,
          }
        });
        if (res.code === 200) {
          this.entryList = res.data.records;
          this.total = res.data.total;
        }
      } catch (error) {
        this.$message.error('加载失败');
      }
    },

    async loadSubjectList() {
      try {
        const res = await request.get('/accounting-subject/tree');
        if (res.code === 200) {
          this.subjectList = res.data;
        }
      } catch (error) {
        this.$message.error('加载科目失败');
      }
    },

    search() {
      this.currentPage = 1;
      this.loadData();
    },

    resetSearch() {
      this.searchForm = {
        voucherNo: '',
        dateRange: []
      };
      this.search();
    },

    handleCurrentChange(val) {
      this.currentPage = val;
      this.loadData();
    },

    showAddDialog() {
      this.dialogTitle = '新增凭证';
      this.resetForm();
      this.dialogVisible = true;
    },

    editEntry(row) {
      this.dialogTitle = '编辑凭证';
      this.entryForm = { ...row };
      // 加载明细数据
      this.loadEntryDetails(row.id);
      this.dialogVisible = true;
    },

    async loadEntryDetails(entryId) {
      try {
        const res = await request.get(`/journal-entry/detail/${entryId}`);
        if (res.code === 200) {
          this.entryDetails = res.data.map(item => ({
            id: item.id,
            subjectId: item.subjectId,
            debitAmount: item.debitAmount,
            creditAmount: item.creditAmount,
            remark: item.remark
          }));
        }
      } catch (error) {
        this.$message.error('加载明细失败');
      }
    },

    resetForm() {
      this.entryForm = {
        id: '',
        voucherNo: '',
        voucherDate: new Date(),
        summary: '',
        totalDebit: 0,
        totalCredit: 0,
        status: 0
      };
      this.entryDetails = [this.createEmptyDetail()];
    },

    createEmptyDetail() {
      return {
        id: '',
        subjectId: '',
        debitAmount: 0,
        creditAmount: 0,
        remark: ''
      };
    },

    addDetail() {
      this.entryDetails.push(this.createEmptyDetail());
    },

    removeDetail(index) {
      this.entryDetails.splice(index, 1);
      this.updateTotal();
    },

    updateTotal() {
      let debitTotal = 0;
      let creditTotal = 0;

      this.entryDetails.forEach(detail => {
        debitTotal += Number(detail.debitAmount) || 0;
        creditTotal += Number(detail.creditAmount) || 0;
      });

      this.entryForm.totalDebit = debitTotal;
      this.entryForm.totalCredit = creditTotal;
    },

    async submitForm() {
      try {
        const data = {
          ...this.entryForm,
          details: this.entryDetails
        };

        let res;
        if (!data.id) {
          res = await request.post('/journal-entry', data);
        } else {
          res = await request.put('/journal-entry', data);
        }

        if (res.code === 200) {
          this.$message.success(res.message || '保存成功');
          this.dialogVisible = false;
          this.loadData();
        } else {
          this.$message.error(res.message || '保存失败');
        }
      } catch (error) {
        this.$message.error('保存失败');
      }
    },

    async viewDetail(row) {
      try {
        const [entryRes, detailsRes] = await Promise.all([
          request.get(`/journal-entry/${row.id}`),
          request.get(`/journal-entry/detail/${row.id}`)
        ]);

        if (entryRes.code === 200 && detailsRes.code === 200) {
          this.selectedEntry = entryRes.data;
          this.selectedEntryDetails = detailsRes.data;
          this.detailVisible = true;
        }
      } catch (error) {
        this.$message.error('加载详情失败');
      }
    },

    async cancelEntry(row) {
      this.$confirm('确定要取消该凭证吗？', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        try {
          const res = await request.delete(`/journal-entry/${row.id}`);
          if (res.code === 200) {
            this.$message.success('取消成功');
            this.loadData();
          } else {
            this.$message.error(res.message || '取消失败');
          }
        } catch (error) {
          this.$message.error('取消失败');
        }
      }).catch(() => {
        this.$message.info('已取消操作');
      });
    },

    formatStatus(row) {
      const statusMap = {
        0: '未过账',
        1: '已过账',
        2: '已取消'
      };
      return statusMap[row.status] || row.status;
    },

    formatStatusText(status) {
      const statusMap = {
        0: '未过账',
        1: '已过账',
        2: '已取消'
      };
      return statusMap[status] || status;
    }
  }
}
</script>

<style scoped>
.journal-entry-container {
  padding: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header h2 {
  margin: 0;
  color: #303133;
}

.search-form {
  margin-bottom: 20px;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}

.add-detail-btn {
  margin: 10px 0;
  text-align: right;
}

.total-section {
  margin-top: 20px;
  text-align: right;
}

.total-item {
  font-weight: bold;
}

.total-item span {
  margin-left: 20px;
}

.error-text {
  color: #f56c6c;
}
</style>
